import React from 'react'

export default function TermsOfUse(props) {
    const Name = props.Name
    return (
        <>
            <p>O usurio se compromete a fazer uso adequado dos contedos e da informao que o site {Name} oferece e com carter enunciativo, mas no limitativo:</p>

            <ul>
                <li>A) No se envolver em atividades que sejam ilegais ou contrrias  boa f e  ordem pblica;</li>
                <li>B) No difundir propaganda ou contedo de natureza racista, xenofbica, casas de apostas, jogos de sorte e azar, qualquer tipo de pornografia ilegal, de apologia ao terrorismo ou contra os direitos humanos;</li>
                <li>C) No causar danos aos sistemas fsicos (hardwares) e lgicos (softwares) do site {Name}, de seus fornecedores ou terceiros, para introduzir ou disseminar vrus informticos ou quaisquer outros sistemas de hardware ou software que sejam capazes de causar danos anteriormente mencionados.</li>
            </ul>
        </>
    );
}