import React from 'react'

export default function Privacy(props) {
    const { Name, Link, Title } = props;
    return (
        <>
            <h1>{Title}</h1>
            <p>A sua privacidade  importante para ns.  poltica do site {Name} respeitar a sua privacidade em relao a qualquer informao sua que possamos coletar no site <a href={Link} target="_blank" rel="noopener">{Name}</a>, e outros sites que possumos e operamos.</p>
            <p>Solicitamos informaes pessoais apenas quando realmente precisamos delas para lhe fornecer um servio. Fazemo-lo por meios justos e legais, com o seu conhecimento e consentimento. Tambm informamos por que estamos coletando e como ser usado.</p>
            <p>Apenas retemos as informaes coletadas pelo tempo necessrio para fornecer o servio solicitado. Quando armazenamos dados, protegemos dentro de meios comercialmente aceitveis para evitar perdas e roubos, bem como acesso, divulgao, cpia, uso ou modificao no autorizados.</p>
            <p>No compartilhamos informaes de identificao pessoal publicamente ou com terceiros, exceto quando exigido por lei.</p>
            <p>O nosso site pode ter links para sites externos que no so operados por ns. Esteja ciente de que no temos controle sobre o contedo e prticas desses sites e no podemos aceitar responsabilidade por suas respectivas polticas de privacidade.</p>
            <p>Voc  livre para recusar a nossa solicitao de informaes pessoais, entendendo que talvez no possamos fornecer alguns dos servios desejados.</p>
            <p>O uso continuado de nosso site ser considerado como aceitao de nossas prticas em torno de privacidade e informaes pessoais. Se voc tiver alguma dvida sobre como lidamos com dados do usurio e informaes pessoais, entre em contato conosco.</p>
            <h2>Poltica de Cookies</h2>
            <h3>O que so cookies?</h3>
            <p>Como  prtica comum em quase todos os sites profissionais, este site usa cookies, que so pequenos arquivos baixados no seu computador, para melhorar sua experincia. Esta pgina descreve quais informaes eles coletam, como as usamos e por que s vezes precisamos armazenar esses cookies. Tambm compartilharemos como voc pode impedir que esses cookies sejam armazenados, no entanto, isso pode fazer o downgrade ou 'quebrar' certos elementos da funcionalidade do site.</p>
            <h3>Como usamos os cookies?</h3>
            <p>Utilizamos cookies por vrios motivos, detalhados abaixo. Infelizmente, na maioria dos casos, no existem opes padro do setor para desativar os cookies sem desativar completamente a funcionalidade e os recursos que eles adicionam a este site.  recomendvel que voc deixe todos os cookies se no tiver certeza se precisa ou no deles, caso sejam usados para fornecer um servio que voc usa.</p>
            <h3>Desativar cookies</h3>
            <p>Voc pode impedir a configurao de cookies ajustando as configuraes do seu navegador (consulte a Ajuda do navegador para saber como fazer isso). Esteja ciente de que a desativao de cookies afetar a funcionalidade deste e de muitos outros sites que voc visita. A desativao de cookies geralmente resultar na desativao de determinadas funcionalidades e recursos deste site. Portanto,  recomendvel que voc no desative os cookies.</p>
            <h3>Cookies que definimos</h3>
            <ul>
                <li>
                    <strong>Cookies relacionados a pesquisas</strong>
                    <br></br>
                    <br></br>
                    Periodicamente, oferecemos pesquisas e questionrios para fornecer informaes interessantes, ferramentas teis ou para entender nossa base de usurios com mais preciso. Essas pesquisas podem usar cookies para lembrar quem j participou numa pesquisa ou para fornecer resultados precisos aps a alterao das pginas.
                    <br></br>
                    <br></br>
                </li>
                <li>
                    <strong>Cookies relacionados a formulrios</strong>
                    <br></br>
                    <br></br>
                    Quando voc envia dados por meio de um formulrio como os encontrados nas pginas de contato ou nos formulrios de comentrios, os cookies podem ser configurados para lembrar os detalhes do usurio para correspondncia futura.                    <br></br>
                    <br></br>
                </li>
                <li>
                    <strong>Cookies de preferncias do site</strong>
                    <br></br>
                    <br></br>
                    Para proporcionar uma tima experincia neste site, fornecemos a funcionalidade para definir suas preferncias de como esse site  executado quando voc o usa. Para lembrar suas preferncias, precisamos definir cookies para que essas informaes possam ser chamadas sempre que voc interagir com uma pgina que for afetada por suas preferncias.                    <br></br>
                </li>
            </ul>
            <h3>Cookies de Terceiros</h3>
            <p>Em alguns casos especiais, tambm usamos cookies fornecidos por terceiros confiveis. A seo a seguir detalha quais cookies de terceiros voc pode encontrar atravs deste site.</p>
            <ul>
                <li>Este site usa o Google Analytics, que  uma das solues de anlise mais difundidas e confiveis da Web, para nos ajudar a entender como voc usa o site e como podemos melhorar sua experincia. Esses cookies podem rastrear itens como quanto tempo voc gasta no site e as pginas visitadas, para que possamos continuar produzindo contedo atraente.<br></br>Para mais informaes sobre cookies do Google Analytics, consulte a pgina oficial do Google Analytics.</li>

                <li>As anlises de terceiros so usadas para rastrear e medir o uso deste site, para que possamos continuar produzindo contedo atrativo. Esses cookies podem rastrear itens como o tempo que voc passa no site ou as pginas visitadas, o que nos ajuda a entender como podemos melhorar o site para voc.</li>

                <li>Periodicamente, testamos novos recursos e fazemos alteraes sutis na maneira como o site se apresenta. Quando ainda estamos testando novos recursos, esses cookies podem ser usados para garantir que voc receba uma experincia consistente enquanto estiver no site, enquanto entendemos quais otimizaes os nossos usurios mais apreciam.</li>

                <li> medida que vendemos produtos,  importante entendermos as estatsticas sobre quantos visitantes de nosso site realmente compram e, portanto, esse  o tipo de dados que esses cookies rastrearo. Isso  importante para voc, pois significa que podemos fazer previses de negcios com preciso que nos permitem analisar nossos custos de publicidade e produtos para garantir o melhor preo possvel.</li>
            </ul>
        
            <h2>Direitos do titular de dados</h2>
            <p>O titular de dados pessoais possui o direito de solicitar do site {Name}, a qualquer momento, mediante requisio formal, informaes referentes aos seus dados.</p>
            <p>O site {Name} ter 14 dias teis para responder s solicitaes dos titulares de dados pessoais. Os pedidos sero analisados conforme previsto em legislao vigente e, por questes legais, algumas solicitaes podem no ser atendidas.</p>
            <p>Os titulares de dados, segundo o texto da LGPD, podem exercer os seus direitos por meio de:</p>
            <ul>
                <li>Confirmao da existncia de tratamento;</li>


                <li>Anonimizao, bloqueio ou eliminao de dados desnecessrios, excessivos ou tratados em desconformidade com o disposto nesta Lei;</li>


                <li>Informao das entidades pblicas e privadas com as quais o controlador realizou uso compartilhado de dados;</li>

                <li>Informao sobre a possibilidade de no fornecer consentimento e sobre as consequncias da negativa;</li>

            </ul>
            <h2>Como exercer os seus direitos de titular de dados?</h2>
            <ul>
                <li>Para as demais solicitaes em relao aos direitos do titular de dados pessoais, entre em contato conosco;</li>
            </ul>

            <h3>Mais informaes</h3>
            <p>Esperemos que esteja esclarecido e, como mencionado anteriormente, se houver algo que voc no tem certeza se precisa ou no, geralmente  mais seguro deixar os cookies ativados, caso interaja com um dos recursos que voc usa em nosso site.</p>
        </>
    );
}